import  { HttpFormdata } from '../../axios/axios'
import { actions } from '../actions/Action';

export const createCoverReveiwsBackground = ({ dispatch, getState }) => next => action => {
    if (action.type === 'SET_COVER_REVEIWS_STYLE') {
        const uid = getState().user.userId, userName = getState().user.userName;
        let formData = new FormData();
        formData.append('profile_reveiws', action.payload.file);
        dispatch(actions.setLoading());
        HttpFormdata.post(`/${userName}/${uid}/upload`, formData).then(async(response) => {
            let url=`url(${response.data.files.profile_reveiws.url})`
            dispatch(actions.setCoverReveiwsBackground( url));
            dispatch(actions.setLoading());
        })
            .catch((error) => {
                console.log(error)
            })
        return;
    }
    return next(action);
};