import React from 'react'
import ReactPlayer from 'react-player'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';



const mapStateToProps = (state) => {
  return {
    currentVideo: state.video.currentVideo,

  };
}

const mapDispatchToProps = (dispatch) => ({


})

export default connect(mapStateToProps, mapDispatchToProps)(function VideoCard(props) {
  const { currentVideo } = props;

  return (
    <div className='player-wrapper'>
     
      <ReactPlayer
        className='react-player'
        url={currentVideo.videoCard.url}
        width='100%'
        height='100%'
        playing={currentVideo.videoCard.playing}
        playbackRate={currentVideo.videoCard.playbackRate ? currentVideo.videoCard.playbackRate : 1}
        // Https://Www.Youtube.Com/Watch?V=Kf7wK0J3MhU
      />

    </div>

  );

});



