// One row with scroll
import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { OnOff } from '../helpComponents/OnOff'
import Slider from '@material-ui/core/Slider';
import AgentDetail from '../helpComponents/AgentDetail'



const mapStateToProps = (state) => {
    return {
        currentVideo: state.video.currentVideo,

    };
}

const mapDispatchToProps = (dispatch) => ({

    setplayingVideo: () => dispatch(actions.setplayingVideo()),
    setSpeedVideo: (speed) => dispatch(actions.setSpeedVideo(speed)),
    setUrlVideo: (url) => dispatch(actions.setUrlVideo(url)),

})

export default connect(mapStateToProps, mapDispatchToProps)(function VideoNav(props) {

    const { setplayingVideo, currentVideo, setSpeedVideo, setUrlVideo } = props;
    return (
        <>
            <div className="container-fluid">

                <div className="row input_footer">
                    <div className="col-8 p-1">
                        <label id="aboutMe" className="title">playing</label>
                    </div>
                    <div className="col-4 p-0 d-flex justify-content-end">
                        <OnOff
                            onChange={setplayingVideo}
                            cheak={currentVideo.videoCard.playing}
                            onChangeOnOff={setplayingVideo}
                            type={"fullName"}
                            idElement="#video"
                        ></OnOff>
                    </div>
                </div>
                <div className="row mt-2">
                    <label className="title">Speed</label>
                </div>
                <div className="row ">
                    <div className="col m=0 p-0 title d-flex align-items-end"> <Slider
                        style={{
                            color: "#EBEAEA"
                        }}
                        onChange={(event) => setSpeedVideo(event.target.innerText)}
                        // value={timeout}
                        defaultValue={currentVideo.videoCard.playbackRate}
                        //  getAriaValueText={valuetext}
                        aria-labelledby="continuous-slider"
                        valueLabelDisplay="auto"
                        step={0.1}
                        min={0.5}
                        max={2}
                    />
                    </div>

                    {/* <input type="number" className="col-3 m-0 p-0 input d-flex align-items-start"
                        value={currentVideo.videoCard.playbackRate}
                        onChange={(event) => setSpeedVideo(event.target.value)}

                    ></input> */}
                </div>
                <div className="row mb-3">
                    <AgentDetail
                        header="enter url video"
                        title={currentVideo.videoCard.url}
                        placeholder="Each Line Url"
                        onChange={setUrlVideo}
                        typeInput="text"
                        name="urlVideo"
                        // error={this.props.validation.errors.mobile}
                        // validationOnChange={this.props.validationOnChange}
                        hiddenOnOff={false}

                    ></AgentDetail>
                </div>
            </div>
        </>
    );
})