import { createStore, combineReducers, applyMiddleware, compose } from 'redux';
import {
    createNewCard, cardSelection, saveNewCard, updateCard, getCards, deleteCard,
    sendEmail, getUid, setCardBeforeServer, setCardAfterServer,checkUniqueCardName
} from '../MiddleWares/CardMiddleWare'
 
import {createCoverReveiwsBackground} from '../MiddleWares/ReviewMiddleWare'


import agentDetails from '../reducers/AgentDetailsStore';
import card from '../reducers/CardStore'
import user from '../reducers/User'
import validation from '../reducers/Validation'
import alert from '../reducers/AlertStore'
import tamplate from '../reducers/TamplateStore'
import gallerySlide from '../reducers/GalleryStore'
import reveiws from '../reducers/ReveiwStore'
import modal from '../reducers/ModalStore'
import video from '../reducers/VideoStore'

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const reducer = combineReducers({
    agentDetails,
    card,
    user,
    validation,
    alert,
    gallerySlide,
    tamplate,
    reveiws,
    modal,
    video
});

const store = createStore(
    reducer,
    composeEnhancers(
    applyMiddleware(
        sendEmail,
        createCoverReveiwsBackground,
        cardSelection,
        createNewCard,
        saveNewCard,
        deleteCard,
        checkUniqueCardName,
        updateCard,
        setCardBeforeServer,
        setCardAfterServer,
        getCards,
        getUid))
    );
window.store = store;
export default store;
store.dispatch({ type: 'UID_INIT' });
// store.dispatch({ type: "CARD_INIT" });
