
import React from 'react';
import '../../Css/Nav.css'
import '../../Css/helpComponentCss/DatePickerColor.css'
import { ChromePicker } from 'react-color';
import reactCSS from 'reactcss';

class Color extends React.Component {
  state = {
    displayColorPicker: false,
  };

  handleClick = () => {
    this.setState({ displayColorPicker: !this.state.displayColorPicker })
  };


  render() {
    const styles = reactCSS({
      'default': {
        color: {
          width: '45px',
          height: '20px',
          borderRadius: '2px',
          background: this.props.color != "" ? this.props.color : "#ffffff",
          border: "1px solid #ffff"
        },
        swatch: {
          padding: '0px',
          background: '#fff',
          borderRadius: '1px',
          boxShadow: '0 0 0 1px rgba(0,0,0,.1)',
          display: 'inline-block',
          cursor: 'pointer',
        },
        popover: {
          position: 'absolute',
          zIndex: '2',
        },
        cover: {
          position: 'fixed',
          top: '0px',
          right: '0px',
          bottom: '0px',
          left: '0px',
        },
      },
    });

    return (
      <div className="row p-0">
        <div>
          <div style={styles.swatch} onClick={this.handleClick}>
            <div style={styles.color} />
          </div>
          {this.state.displayColorPicker ? <div style={styles.popover}>
            <div style={styles.cover} onClick={this.handleClick} />
            <ChromePicker color={this.props.color != "" ? this.props.color : "#ffffff"} onChange={(event) => this.props.onchangeColor(event.hex)}
            />
          </div> : null}
        </div>


      </div>


    );
  }
}

export default Color;