import React from 'react';
import { withRouter } from "react-router";
import Card from './Card'
import CardMobile from './CardMobile'



// e 1
export default withRouter(function CardPreview(props) {
const  screenSize =parseInt(props.match.params["screenSize"]?props.match.params.screenSize:"");
    return (
        <>
         {screenSize===0?<CardMobile  screenSizep={screenSize} preview={true}></CardMobile>
            : <Card screenSizep={screenSize} showState={true} preview={true}></Card>}
        </>
    );
})
